.film-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: -20px;
  padding: 0;
  margin: 0;
  min-width: 280px;
  max-width: 100vw;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (min-width: 768px) {
  .film-gallery {
    flex-wrap: wrap;
    margin-left: -30px;
  }
}

.film-card {
  max-width: 293px;
  margin-top: 20px;
  border: 1px solid #000;
}
@media screen and (min-width: 768px) {
  .film-card {
    display: flex;
    flex-basis: calc((100% - 60px) / 2);
    margin-top: 30px;
    margin-left: 30px;
    width: 280px;
  }
}
@media screen and (min-width: 1024px) {
  .film-card {
    display: flex;
    flex-basis: calc((100% - 90px) / 3);
    width: 274px;
  }
}
